<template>
  <v-container
    fluid
    grid-list-md>
    <v-layout
      row
      wrap>
      <v-flex md12>
        <v-form
          novalidate
          enctype="multipart/form-data"
          @submit.prevent="validateBeforeSubmit">
          <material-card
            color="primary"
            title="Arquivo Retorno">
            <v-card class="elevation-0">
              <v-card-text>
                <v-layout
                  row
                  wrap>
                  <v-flex
                    md12
                  >
                    TODO add aqui, os bancos para o usuário selecionar e o sistema fazer o tratamento correto do arquivo
                  </v-flex>
                  <v-flex
                    md12
                  >
                    <input
                      id="anexar"
                      type="file"
                      class="caption"
                      @change="anexar($event)"
                    >
                  </v-flex>
                </v-layout>
              </v-card-text>
              <v-card-actions>
                <v-spacer />
                <v-btn
                  :loading="loadingBtn"
                  color="primary"
                  type="submit"
                >Enviar</v-btn>
              </v-card-actions>
            </v-card>
          </material-card>
        </v-form>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
import { BoletoBusiness } from '../../business'

export default {
  metaInfo: {
    titleTemplate: '%s - Arquivo Retorno'
  },
  data () {
    return {
      file: {},
      formData: null,
      loadingBtn: false
    }
  },
  methods: {
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.loadingBtn = true
          BoletoBusiness.uploadArquivoRetorno(this.formData)
            .then(response => {
              this.$root.showSucesso('Documento enviado com sucesso')
            })
            .catch(error => this.$root.showErro(error.data))
            .finally(() => {
              this.loadingBtn = false
            })
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    },
    anexar (event) {
      if (event && event.target && event.target.files.length > 0) {
        this.file = event.target.files[0]
        this.formData = new FormData()
        this.formData.append('file', this.file)
        this.formData.append('name', this.file.name)
      }
    }
  }
}
</script>
